/* Treat all as divs */
.table-container {
    overflow-x: unset;
    overflow-y: unset;
    max-height: 100000px;
}

.table-container .table {
    border-collapse: collapse;
}

.table-container thead {
    position: sticky;
    top: 0;
}

.table-container tfoot {
    background-color: #f2f2f2;
    position: sticky;
    bottom: 0;
}

.table-container th,
td {
    padding: 2px 2px;
    text-align: left;
}

.invoice-template,
.invoice {
    color: black;
}

.invoice tr td {
    border: 0;
    padding-top: 2px;
    padding-bottom: 2px;
}

.bill-total tr td {
    border: 0;
    color: black;
}

.bill-footer tr td {
    border: 0;
    text-align: center;
}

.nav-link {
