    border: 1px solid black;
    border-collapse: collapse;
}

@media print {

    html,
    body {
        width: 200mm;
        height: 200mm;
    }

    @page {
        size: A4;
    }

    .printable {
        margin: 2px;
    }

    .printable table {
        width: 99%;
        margin: 5px 5px;
        font-size: 14px;
    }

    .printable table tr td {
        padding: 0 5px;
    }

    .report-header table tr td {
        width: 20%;
        border: 0;
        padding: 2px 5px;
    }

    .datatable-download table {
        width: 100%;
        margin: 5px;
        display: block;
        font-size: 9px;
    }

    .datatable-download table tr td {
        padding: 0px 5px;
        border: 1px solid black;
        border-collapse: collapse;
    }

    .datatable-download table tr th {
        border: 1px solid black;
        border-collapse: collapse;
    }

    .invoice-template,
    .invoice {
        color: black;
    }

    .invoice tr td {
        border: 0;
        padding-top: 2px;
        padding-bottom: 2px;
    }

    .bill-total tr td {
        border: 0;
        color: black;
    }

    .bill-footer tr td {
        border: 0;
        text-align: center;
    }
}

