/* Treat all as divs */
.table-container {
  overflow-x: auto;
  overflow-y: auto;
  max-height: 400px;
}

.table-container .table {
  border-collapse: collapse;
}

.table-container thead {
  position: sticky;
  top: 0;
}

.table-container tfoot {
  background-color: #f2f2f2;
  position: sticky;
  bottom: 0;
}

.table-container th,
td {
  padding: 2px 2px;
  text-align: left;
}

#invoice-template,
.invoice {
  color: black;
}

.invoice tr td {
  border: 0;
  padding-top: 2px;
  padding-bottom: 2px;
}

.bill-total tr td {
  border: 0;
  color: black;
}

.bill-footer tr td {
  border: 0;
  text-align: center;
}

.nav-link {
  font-family: Arial, serif;
  font-weight: bold;
}

.datatable-download table {
  width: 100%;
  margin: 5px;
  display: none;
  font-size: 9px;
}

.datatable-download table tbody tr td {
  padding: 1px 5px;
  border: 1px solid black;
  border-collapse: collapse;
}

.datatable-download table thead tr th {
  border: 1px solid black;
  border-collapse: collapse;
}

.scroll-report {
  overflow: auto;
  width: 100%;
}

#patient-report-print-content table tbody tr td {
  padding: 1px 5px;
  border: 1px solid black;
  border-collapse: collapse;
}

#patient-report-print-content thead tbody tr td {
  padding: 1px 5px;
  border: 1px solid black;
  border-collapse: collapse;
}

@media print {
  @page {
    size: A4;
    margin: 0;
  }

  .printable {
    margin: 2px;
  }

  .printable table {
    width: 99%;
    margin: 5px 5px;
  }

  .printable table tr td {
    padding: 0 5px;
  }

  .report-header table tr td {
    width: 20%;
    border: 0;
    padding: 2px 5px;
  }

  .datatable-download table {
    width: 100%;
    margin: 5px;
    display: block;
    font-size: 9px;
  }

  .datatable-download table tr td {
    padding: 1px 5px;
    border: 1px solid black;
    border-collapse: collapse;
  }

  .datatable-download table tr th {
    border: 1px solid black;
    border-collapse: collapse;
  }

  #invoice-template,
  .invoice {
    color: black;
  }

  .invoice tr td {
    border: 0;
    padding-top: 2px;
    padding-bottom: 2px;
  }

  .bill-total tr td {
    border: 0;
    color: black;
  }

  .bill-footer tr td {
    border: 0;
    text-align: center;
  }
}


